.wave-container {
  height: 100%;
  width: 100%;
  background: #3586ff;
  overflow: hidden;
  z-index: 100;
  border-radius: 15px;
  margin: 0 10px;
  position: relative;
  transition: 5s; }
  .wave-container .title {
    font-family: "Righteous", cursive;
    position: absolute;
    top: 5px;
    left: 5px; }
  .wave-container .sub-header {
    font-family: "Righteous", cursive;
    width: 50%;
    margin: 0 auto;
    margin-top: 45%; }
    .wave-container .sub-header span {
      color: #5a3bb0;
      mix-blend-mode: multiply;
      letter-spacing: 1px; }

@-webkit-keyframes dyebackground {
  from {
    -webkit-filter: hue-rotate(0deg) brightness(0.8);
            filter: hue-rotate(0deg) brightness(0.8); }
  to {
    -webkit-filter: hue-rotate(360deg) brightness(0.8);
            filter: hue-rotate(360deg) brightness(0.8); } }

@keyframes dyebackground {
  from {
    -webkit-filter: hue-rotate(0deg) brightness(0.8);
            filter: hue-rotate(0deg) brightness(0.8); }
  to {
    -webkit-filter: hue-rotate(360deg) brightness(0.8);
            filter: hue-rotate(360deg) brightness(0.8); } }
  .wave-container .air {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background: url(https://1.bp.blogspot.com/-xQUc-TovqDk/XdxogmMqIRI/AAAAAAAACvI/AizpnE509UMGBcTiLJ58BC6iViPYGYQfQCLcBGAsYHQ/s1600/wave.png);
    background-size: 1000px 100px;
    -webkit-transform: scaleY(1);
            transform: scaleY(1); }
  .wave-container .air.air1 {
    z-index: 1000;
    opacity: 1;
    bottom: 0; }
  .wave-container .air.air2 {
    z-index: 999;
    opacity: 0.5;
    bottom: 10px;
    background-position-x: 0px; }
  .wave-container .air.air3 {
    z-index: 998;
    opacity: 0.2;
    bottom: 15px;
    background-position-x: 0px; }
  .wave-container .air.air4 {
    z-index: 997;
    opacity: 0.7;
    bottom: 20px;
    background-position-x: 0px; }

@-webkit-keyframes wave {
  0% {
    background-position-x: 0px; }
  100% {
    background-position-x: 1000px; } }

@keyframes wave {
  0% {
    background-position-x: 0px; }
  100% {
    background-position-x: 1000px; } }

@-webkit-keyframes wave2 {
  0% {
    background-position-x: 0px; }
  100% {
    background-position-x: -1000px; } }

@keyframes wave2 {
  0% {
    background-position-x: 0px; }
  100% {
    background-position-x: -1000px; } }
  .wave-container:hover {
    -webkit-animation: 120s dyebackground linear infinite;
            animation: 120s dyebackground linear infinite; }
    .wave-container:hover .air.air1 {
      -webkit-animation: wave 35s linear infinite;
              animation: wave 35s linear infinite;
      -webkit-animation-delay: 0s;
              animation-delay: 0s; }
    .wave-container:hover .air.air2 {
      -webkit-animation: wave2 20s linear infinite;
              animation: wave2 20s linear infinite;
      -webkit-animation-delay: -5s;
              animation-delay: -5s; }
    .wave-container:hover .air.air3 {
      -webkit-animation: wave 35s linear infinite;
              animation: wave 35s linear infinite;
      -webkit-animation-delay: -2s;
              animation-delay: -2s; }
    .wave-container:hover .air.air4 {
      -webkit-animation: wave2 10s linear infinite;
              animation: wave2 10s linear infinite;
      -webkit-animation-delay: -5s;
              animation-delay: -5s; }

